import React, { Component } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";

// Loader
import Loader from "../../components/Loader/Loader";

// Components
import LeftBar from "../../components/LeftBar/LeftBar";
import Navigation from "../../components/Navigation/Navigation";

// Icons
import { search } from "../../helper/icons";

// Animation
import Fade from "react-reveal/Fade";

// Server API
import { getInfo } from "../../api/info";
import { getProjects } from "../../api/portfolio";
import { getCategories } from "../../api/categories";

class Portfolio extends Component {
  state = {
    loading: true,
    getAllPortfolio: [],
    portfolio: [],

    // Site Title
    siteTitle: "",

    categories: [],
    selectedCat: "All",
  };

  getData = async () => {
    const info = await getInfo();
    const portfolio = await getProjects();
    const categories = await getCategories();
    const categoriesNames = categories.data.map((category) => category.name);

    if (!info.success || !portfolio.success || !categories.success) {
      return toast.error("Something wrong, try again");
    }

    this.setState({
      siteTitle: info.data[3].option_value,
      getAllPortfolio: portfolio.data,
      portfolio: portfolio.data,
      categories: ["All", ...categoriesNames],
    });
  };

  async componentDidMount() {
    await this.getData();

    this.setState({
      loading: false,
    });

    // Naming The Page
    const siteTitle = this.state?.siteTitle;
    document.title = `${siteTitle} - Portfolio`;
  }

  filterItems = (e, category) => {
    e.preventDefault();

    if (category === "All") {
      this.setState({
        selectedCat: category,
        portfolio: this.state.getAllPortfolio,
      });
    } else {
      this.setState({
        selectedCat: category,
        portfolio: this.state.getAllPortfolio.filter(
          (item) => item.portfolio_category.name === category
        ),
      });
    }
  };

  render() {
    // Import Styling
    require("./Portfolio.scss");

    const portfolioItems = this.state.portfolio.map((item) => (
      <div className="col-md-4" key={item.id}>
        <Fade bottom>
          <Link to={`/portfolio/${item.ID}`}>
            <div className="work">
              <div className="thumbnail">
                <div className="overlay">{search}</div>
                <img
                  src={process.env.REACT_APP_BASE_URL + item.thumbnail}
                  alt={item.name}
                />
              </div>
              <h3>{item.name}</h3>
              <span>{item.portfolio_category.name}</span>
            </div>
          </Link>
        </Fade>
      </div>
    ));

    const categoriesList = this.state.categories.map((cat) => (
      // eslint-disable-next-line
      <a
        key={cat}
        href="#"
        className={cat === this.state.selectedCat ? "active" : ""}
        onClick={(e) => this.filterItems(e, cat)}
      >
        {cat}
      </a>
    ));

    return (
      <div className="Portfolio">
        {this.state.loading && <Loader />}
        <LeftBar />

        <div className="col-md-9 offset-md-3">
          <div className="all-content">
            <Fade bottom>
              <section>
                <h3>Portfolio</h3>
                <div className="categories">{categoriesList}</div>
              </section>
            </Fade>

            <div className="works">
              <div className="row">{portfolioItems}</div>
            </div>
          </div>
        </div>

        <Navigation />
      </div>
    );
  }
}

export default Portfolio;
