import React, { useEffect } from "react";
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'

// Pages
import Home from "./pages/Home/Home";
import About from "./pages/About/About";
import Portfolio from "./pages/Portfolio/Portfolio";
import SinglePortfolio from "./pages/SinglePortfolio/SinglePortfolio";
import Blog from "./pages/Blog/Blog";
import SingleBlog from "./pages/SingleBlog/SingleBlog";
import Contact from "./pages/Contact/Contact";

// Icons
import { squaresMain } from "./helper/icons";

// Server API
import { getInfo } from "./api/info";

const App = () => {
  useEffect(() => {
    changeColor();
  }, []);

  const changeColor = async () => {
    const info = await getInfo();
    const color = info?.data[4]?.option_value;
    document.documentElement.style.setProperty(
      "--bg-color",
      `#${color}`,
      "important"
    );
  };

  return (
    <div className="Main">
      {squaresMain}
      
      <Router>
        <Routes>
          <Route path="/" element={<Home/>} exact />
          <Route path="/about" element={<About/>} exact />
          <Route path="/portfolio" element={<Portfolio/>} exact />
          <Route path="/portfolio/:id" element={<SinglePortfolio/>} exact />
          <Route path="/blog" element={<Blog/>} exact />
          <Route path="/blog/:id" element={<SingleBlog/>} exact />
          <Route path="/contact" element={<Contact/>} exact />
        </Routes>
      </Router>
    </div>
  );
};

export default App;
