import React, { Component } from "react";
import { toast } from "react-toastify";
import Gravatar from "react-gravatar";

// Loader
import Loader from "../../components/Loader/Loader";

// Components
import LeftBar from "../../components/LeftBar/LeftBar";
import Navigation from "../../components/Navigation/Navigation";

// Icons
import { send } from "../../helper/icons";

// Animation
import Fade from "react-reveal/Fade";

// Server API
import { getInfo } from "../../api/info";
import { getArticle } from "../../api/blog";
import { getComments, addComment } from "../../api/comments";

class SingleBlog extends Component {
  state = {
    loading: true,
    id: this.props.match.params.id,
    siteTitle: "",
    content: {},
    comments: [],

    // Comment Inputs
    name: "",
    email: "",
    comment: "",
  };

  getData = async () => {
    const info = await getInfo();
    const article = await getArticle(this.state.id);
    const comments = await getComments(this.state.id);

    if (!article.success || !info.success) {
      return toast.error("Something wrong, try again");
    }

    this.setState({
      siteTitle: info.data[3].option_value,
      content: article.data,
      comments: comments.data,
    });
  };

  addComment = async (e) => {
    e.preventDefault();

    if (!this.state.name || !this.state.email || !this.state.comment) {
      return toast.error("All fields are requires");
    }

    const sendComment = await addComment({
      name: this.state.name,
      comment: this.state.comment,
      email: this.state.email,
      blogID: this.state.id,
    });

    if (!sendComment.success) {
      return toast.error(sendComment.message);
    }

    await this.getData();
    toast.success("Comment added successfully");

    this.setState({
      name: "",
      email: "",
      comment: "",
    });
  };

  async componentDidMount() {
    await this.getData();

    this.setState({
      loading: false,
    });

    // Naming The Page
    const siteTitle = this.state?.siteTitle;
    document.title = `${siteTitle} - ${this.state.content.title}`;
  }

  render() {
    // Import Styling
    require("./SingleBlog.scss");

    const parentComments = this.state?.comments?.filter(
      (comment) => comment.parentID === null
    );

    const repliesComments = this.state?.comments?.filter(
      (comment) => comment.parentID !== null
    );

    const comments = parentComments?.map((comment) => {
      const replies = repliesComments?.map((reply) => {
        if (reply.parentID === comment.ID) {
          return (
            <li>
              <div className="comment reply">
                <Gravatar email={reply.email} default="monsterid" size={100} />
                <div className="text">
                  <div className="name">{reply.name}</div>
                  <div className="body">{reply.comment}</div>
                </div>
              </div>
            </li>
          );
        }
      });

      return (
        <li key={comment.id}>
          <div className="comment">
            <Gravatar email={comment.email} default="monsterid" size={100} />
            <div className="text">
              <div className="name">{comment.name}</div>
              <div className="body">{comment.comment}</div>
              {/*<button>{reply} Reply</button>*/}
            </div>
          </div>

          <ol>{replies}</ol>
        </li>
      );
    });

    return (
      <div className="SingleBlog">
        {this.state.loading && <Loader />}
        <LeftBar />

        <div className="col-md-9 offset-md-3">
          <div className="all-content">
            <section>
              <Fade bottom>
                <h3>{this.state.content.title}</h3>
              </Fade>

              <Fade bottom>
                <div className="entry">
                  <div
                    className="wrapper"
                    dangerouslySetInnerHTML={{
                      __html: this.state.content.content,
                    }}
                  />
                  <div className="the-end">
                    <span>The End</span>
                  </div>
                </div>
              </Fade>

              <Fade bottom>
                <div className="comments">
                  {this.state?.comments?.length ? (
                    <div className="title">
                      Comments ({this.state?.comments?.length})
                    </div>
                  ) : (
                    ""
                  )}

                  <ul>{comments}</ul>
                  <div className="title mt-5">Write a comment</div>
                  <form onSubmit={this.addComment}>
                    <div className="row">
                      <div className="col-md-6">
                        <input
                          type="text"
                          name="full-name"
                          id="full-name"
                          placeholder="Your name"
                          onChange={(e) =>
                            this.setState({ name: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-6">
                        <input
                          type="email"
                          name="e-mail"
                          id="e-mail"
                          placeholder="Your email"
                          onChange={(e) =>
                            this.setState({ email: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-12 position-relative">
                        <textarea
                          name="comment"
                          id="comment"
                          placeholder="Write a comment..."
                          onChange={(e) =>
                            this.setState({ comment: e.target.value })
                          }
                        />
                        <button className="send" type="submit">
                          {send}
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </Fade>
            </section>
          </div>
        </div>

        <Navigation />
      </div>
    );
  }
}

export default SingleBlog;
