import React, { Component } from "react";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";

// Loader
import Loader from "../../components/Loader/Loader";

// Components
import LeftBar from "../../components/LeftBar/LeftBar";
import Navigation from "../../components/Navigation/Navigation";

// Helpers
import { trimText } from "../../helper/functions";

// Icons
import { newspaper } from "../../helper/icons";

// Animation
import Fade from "react-reveal/Fade";

// Server API
import { getArticles } from "../../api/blog";
import { getInfo } from "../../api/info";

class Blog extends Component {
  state = {
    loading: true,
    siteTitle: "",
    articles: [],
  };

  getData = async () => {
    const articles = await getArticles();
    const info = await getInfo();

    if (!articles.success || !info.success) {
      return toast.error("Something wrong, try again");
    }

    this.setState({
      siteTitle: info.data[3].option_value,
      articles: articles.data,
    });
  };

  async componentDidMount() {
    await this.getData();
    this.setState({
      loading: false,
    });

    // Naming The Page
    const siteTitle = this.state?.siteTitle;
    document.title = `${siteTitle} - Blog`;
  }

  render() {
    // Import Styling
    require("./Blog.scss");
    const monthNames = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];

    const articles = this.state.articles.map((article) => {
      const excerpt = trimText(
        article.content.replace(/<\/?[^>]+(>|$)/g, ""),
        100
      );
      const day = new Date(article.createdAt).getDate();
      const month = new Date(article.createdAt).getMonth();
      return (
        <Fade bottom key={article.id}>
          <div className="col-md-6">
            <Link to={`/blog/${article.ID}`}>
              <div className="blog-card">
                <div className="thumb">
                  <div className="date">
                    <span>{day}</span>
                    {monthNames[month]}
                  </div>
                  <div className="overlay">{newspaper}</div>
                  <img
                    src={process.env.REACT_APP_BASE_URL + article.thumbnail}
                    alt={article.title}
                  />
                </div>
                <div className="text">
                  <h2>{article.title}</h2>
                  <p dangerouslySetInnerHTML={{__html: excerpt}} />
                </div>
              </div>
            </Link>
          </div>
        </Fade>
      );
    });

    return (
      <div className="Blog">
        {this.state.loading && <Loader />}
        <LeftBar />

        <div className="col-md-9 offset-md-3">
          <div className="all-content">
            <section>
              <Fade bottom>
                <h3>Blog</h3>
              </Fade>

              <div className="cards">
                <div className="row">{articles}</div>
              </div>
            </section>
          </div>
        </div>

        <Navigation />
      </div>
    );
  }
}

export default Blog;
