import React, { Component } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";

// Loader
import Loader from "../../components/Loader/Loader";

// Animation
import Tada from "react-reveal/Tada";
import Fade from "react-reveal/Fade";

// Icons
import { downloadIcon, contactIcon } from "../../helper/icons";

// Components
import LeftBar from "../../components/LeftBar/LeftBar";
import Navigation from "../../components/Navigation/Navigation";

// Helpers
import { trimText } from "../../helper/functions";

// Server API
import { getInfo } from "../../api/info";

class Home extends Component {
  state = {
    loading: true,
    info: [],
  };

  getInfo = async () => {
    const info = await getInfo();

    if (!info?.success) {
      return toast.error("Something wrong, try again");
    }

    this.setState({ info: info.data });
  };

  async componentDidMount() {
    await this.getInfo();

    this.setState({
      loading: false,
    });

    // Naming The Page
    const siteTitle = this.state?.info[3]?.option_value;
    document.title = `${siteTitle} - Home`;
  }

  render() {
    // Import Styling
    require("./Home.scss");

    return (
      <div className="Home">
        {this.state.loading && <Loader />}
        <LeftBar />

        {/* Main Content */}
        <div className="col-md-9 offset-md-3">
          <div className="all-content">
            <div className="row align-items-center">
              <div className="col-md-5">
                <Tada>
                  <div
                    className="pp"
                    style={{
                      backgroundImage: `url(${
                        process.env.REACT_APP_BASE_URL +
                        this.state?.info[2]?.option_value
                      })`,
                    }}
                  />
                </Tada>
              </div>
              <div className="col-md-7">
                <Fade>
                  <div className="details">
                    <h3>I'm</h3>
                    <h1>{this.state?.info[11]?.option_value}</h1>
                    <span>{this.state?.info[10]?.option_value}</span>
                    <p>{trimText(this.state?.info[9]?.option_value, 275)}</p>
                    <div className="links">
                      <a
                        href={
                          process.env.REACT_APP_BASE_URL +
                          this.state?.info[0]?.option_value
                        }
                        download={true}
                        className="download"
                      >
                        Download CV {downloadIcon}
                      </a>
                      <Link to="/contact" className="contact">
                        Contact Me {contactIcon}
                      </Link>
                    </div>
                  </div>
                </Fade>
              </div>
            </div>
          </div>
        </div>

        <Navigation />
      </div>
    );
  }
}

export default Home;
