import React, { Component } from "react";
import { toast } from "react-toastify";

// Loader
import Loader from "../../components/Loader/Loader";

// Components
import LeftBar from "../../components/LeftBar/LeftBar";
import Navigation from "../../components/Navigation/Navigation";

// Animation
import Fade from "react-reveal/Fade";

// Server API
import { getInfo } from "../../api/info";
import { sendEmail } from "../../api/email";

class Contact extends Component {
  state = {
    loader: true,
    siteTitle: "",
    map: "",

    // Inputs
    name: "",
    email: "",
    message: "",

    // Loader
    loading: false,
  };

  getData = async () => {
    const info = await getInfo();

    if (!info.success) {
      return toast.error("Something wrong, try again");
    }

    this.setState({
      siteTitle: info.data[3].option_value,
      map: info.data[5].option_value,
    });
  };

  sendMessage = async (e) => {
    e.preventDefault();
    this.setState({
      loading: true,
    });

    if (!this.state.name || !this.state.email || !this.state.message) {
      toast.error("All fields are requires");
      return this.setState({
        loading: false,
      });
    }

    const message = `
      <strong>Name:</strong> ${this.state.name} <br /> 
      <strong>Email:</strong> ${this.state.email} <br /> 
      <strong>Message:</strong> ${this.state.message} 
    `;

    const sendMessage = await sendEmail({
      text: message,
    });

    if (!sendMessage.success) {
      toast.error(sendMessage.message);
      return this.setState({
        loading: false,
      });
    }

    toast.success(sendMessage.message);
    this.setState({
      loading: false,
    });
  };

  async componentDidMount() {
    await this.getData();

    this.setState({
      loader: false,
    });

    // Naming The Page
    const siteTitle = this.state?.siteTitle;
    document.title = `${siteTitle} - Contact`;
  }

  render() {
    // Import Styling
    require("./Contact.scss");

    return (
      <div className="Contact">
        {this.state.loader && <Loader />}
        <LeftBar />

        <div className="col-md-9 offset-md-3">
          <div className="all-content">
            <section>
              <h3>Contact Me</h3>

              <div className="entry">
                <Fade bottom>
                  <div
                    className="map"
                    dangerouslySetInnerHTML={{ __html: this.state.map }}
                  />
                </Fade>
                <Fade bottom>
                  <form onSubmit={this.sendMessage}>
                    <div className="row">
                      <div className="col-md-6">
                        <input
                          type="text"
                          name="full-name"
                          id="full-name"
                          placeholder="Your name"
                          onChange={(e) =>
                            this.setState({ name: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-6">
                        <input
                          type="email"
                          name="e-mail"
                          id="e-mail"
                          placeholder="Your email"
                          onChange={(e) =>
                            this.setState({ email: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-12 position-relative">
                        <textarea
                          name="comment"
                          id="comment"
                          rows={7}
                          placeholder="Write a message..."
                          onChange={(e) =>
                            this.setState({ message: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-3">
                        <button className="send" type="submit">
                          {this.state.loading ? (
                            <i className="fas fa-spinner fa-spin" />
                          ) : (
                            "Send Message"
                          )}
                        </button>
                      </div>
                    </div>
                  </form>
                </Fade>
              </div>
            </section>
          </div>
        </div>

        <Navigation />
      </div>
    );
  }
}

export default Contact;
