import React, { Component } from "react";
import { toast } from "react-toastify";

// Slick Slider
import Slider from "react-slick";

// Loader
import Loader from "../../components/Loader/Loader";

// Animation
import Fade from "react-reveal/Fade";

import "../../assets/scss/slick/slick.scss";

// Icons
import { clock, quote, location, arrow } from "../../helper/icons";

// Components
import LeftBar from "../../components/LeftBar/LeftBar";
import Navigation from "../../components/Navigation/Navigation";

// Server API
import { getInfo } from "../../api/info";
import { getServices } from "../../api/services";
import { getExperiences } from "../../api/work-experiences";
import { getTestimonials } from "../../api/testimonials";
import { getSkills } from "../../api/skills";

class About extends Component {
  state = {
    loading: true,
    scrollY: 0,
    thePosition: 0,

    // Site Title
    siteTitle: "",

    // Data
    about: "",
    services: [],
    experiences: [],
    testimonials: [],
    skills: [],
  };

  getData = async () => {
    const info = await getInfo();
    const services = await getServices();
    const experiences = await getExperiences();
    const testimonials = await getTestimonials();
    const skills = await getSkills();

    if (
      !info.success ||
      !services.success ||
      !experiences.success ||
      !testimonials.success ||
      !skills.success
    ) {
      return toast.error("Something wrong, try again");
    }

    this.setState({
      siteTitle: info.data[3].option_value,
      about: info.data[9].option_value,
      services: services.data,
      experiences: experiences.data,
      testimonials: testimonials.data,
      skills: skills.data,
    });
  };

  async componentDidMount() {
    await this.getData();
    this.setState({
      loading: false,
    });

    // Naming The Page
    const siteTitle = this.state?.siteTitle;
    document.title = `${siteTitle} - About`;

    // Get The Scroll Pixels on Scroll
    window.addEventListener("scroll", this.listenToScroll);
  }

  listenToScroll = () => {
    const winScroll =
      document.body.scrollTop || document.documentElement.scrollTop;

    this.setState({
      thePosition: winScroll,
    });
  };

  render() {
    const settings = {
      nextArrow: <button type="button">{arrow}</button>,
      prevArrow: <button type="button">{arrow}</button>,
      adaptiveHeight: true
    };

    const services = this.state.services.map((service) => (
      <div className="col-md-4" key={service.id}>
        <div className="single-service">
          <div className="icon">
            <img
              src={process.env.REACT_APP_BASE_URL + service.icon}
              alt={service.name}
            />
          </div>
          <h1>{service.name}</h1>
          <p>{service.description}</p>
        </div>
      </div>
    ));

    const workExperiences = this.state.experiences.map((work) => (
      <div className="exp" key={work.id}>
        <h1>
          {work.title} at <span>{work.company}</span>
        </h1>
        <div className="meta">
          <div className="info location">
            {location} <span>{work.location}</span>
          </div>
          <div className="info time">
            {clock}{" "}
            <span>
              {new Date(work.from).toDateString()} -{" "}
              {work.to ? new Date(work.to).toDateString() : "Present"}
            </span>
          </div>
        </div>
        <p>{work.description}</p>
      </div>
    ));

    const testimonials = this.state.testimonials.map((item) => (
      <div className="slide-wrapper" key={item.id}>
        <div className="single-testi">
          <div
            className="img"
            style={{
              backgroundImage: `url(${
                process.env.REACT_APP_BASE_URL + item.photo
              })`,
            }}
          />
          <div className="text">
            <div className="title">{item.name}</div>
            <p>{item.description}</p>
          </div>

          {quote}
        </div>
      </div>
    ));

    const skills = this.state.skills.map((skill) => (
      <div className="col-md-6" key={skill.id}>
        <div className="single-skill">
          <h4>
            {skill.name} <span>{skill.percentage}%</span>
          </h4>
          <div className="bar">
            <div
              className="level"
              style={{
                width:
                  this.state.thePosition >= 500 ? `${skill.percentage}%` : "0",
              }}
            />
          </div>
        </div>
      </div>
    ));

    // Import Styling
    require("./About.scss");

    return (
      <div className="About">
        {this.state.loading && <Loader />}
        <LeftBar />

        {/* Main Content */}
        <div className="col-md-9 offset-md-3">
          <div className="all-content">
            {/* About Me */}
            <Fade bottom>
              <section className="about-me">
                <h3>About Me</h3>
                <p>{this.state.about}</p>
              </section>
            </Fade>

            {/* Services */}
            <Fade bottom>
              <section className="services">
                <h3>Services</h3>
                <div className="row">{services}</div>
              </section>
            </Fade>

            {/* Work Experiences */}
            <Fade bottom>
              <section className="work-experience">
                <h3>Work Experience</h3>

                <div className="works">
                  {workExperiences}

                  <div className="line" />
                </div>
              </section>
            </Fade>

            {/* Testimonials */}
            <Fade bottom>
              <section className="testimonials">
                <h3>Testimonials</h3>

                <Slider {...settings}>{testimonials}</Slider>
              </section>
            </Fade>

            {/* Skills */}
            <Fade bottom>
              <section className="skills">
                <h3>Skills</h3>

                <div className="wrapper">
                  <div className="row">{skills}</div>
                </div>
              </section>
            </Fade>
          </div>
        </div>

        <Navigation />
      </div>
    );
  }
}

export default About;
