import React, { Component } from "react";
import { Link } from "react-router-dom";

// Import Styling
import "./LeftBar.scss";

// Icons
import {
  dribbble,
  facebook,
  instagram,
  squares,
  twitter,
} from "../../helper/icons";

//Server API
import { getInfo } from "../../api/info";
import { toast } from "react-toastify";

class LeftBar extends Component {
  state = {
    menuOpened: false,
    info: [],
  };

  getInfo = async () => {
    const info = await getInfo();
    if (!info?.success) {
      return toast.error("Something wrong, try again");
    }

    this.setState({ info: info.data });
  };

  async componentDidMount() {
    await this.getInfo();
  }

  render() {
    const social = this.state?.info.length
      ? JSON.parse(this.state?.info[7]?.option_value)
      : null;

    return (
      <div
        className={`left-bar col-md-3 ${
          this.state.menuOpened ? "menuOpened" : ""
        }`}
      >
        <div className="wrapper">
          <div className="top-bar-wrapper">
            <Link to="/">
              <img
                className="logo"
                src={process.env.REACT_APP_BASE_URL + this.state?.info[1]?.option_value}
                alt="Sek"
              />
            </Link>

            <button
              onClick={() =>
                this.setState({ menuOpened: !this.state.menuOpened })
              }
              className="mobile-menu"
            >
              <i className="fas fa-bars" />
            </button>
          </div>

          {squares}

          <div className="meta">
            <div className="info">
              <span>Email</span>
              {this.state?.info[8]?.option_value}
            </div>
            <div className="info">
              <span>Phone</span>
              {this.state?.info[6]?.option_value}
            </div>
            <div className="social">
              <a
                href={social?.dribbble}
                target="_blank"
                rel="noopener noreferrer"
              >
                {dribbble}
              </a>

              <a
                href={social?.facebook}
                target="_blank"
                rel="noopener noreferrer"
              >
                {facebook}
              </a>

              <a
                href={social?.instagram}
                target="_blank"
                rel="noopener noreferrer"
              >
                {instagram}
              </a>

              <a
                href={social?.twitter}
                target="_blank"
                rel="noopener noreferrer"
              >
                {twitter}
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LeftBar;
